@import url(https://fonts.googleapis.com/css?family=Assistant:400,700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.show{display:block}.hide{display:none}#page-wrapper{display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%;justify-content:space-around;background-color:#011627;color:#fdfffc;font:16px "Assistant", sans-serif;padding:0.5rem;grid-gap:0.5rem;gap:0.5rem;min-height:100vh;box-sizing:border-box}@media only screen and (min-width: 720px){#page-wrapper{padding:2rem;grid-gap:2rem;gap:2rem}}#display{display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%;max-width:800px}#status{font-weight:400;font-feature-settings:"c2sc";font-variant:small-caps;margin:0;padding:0}#counter{font-size:25vw}@media only screen and (min-width: 480px){#counter{font-size:20vw}}@media only screen and (min-width: 1080px){#counter{font-size:20vh}}#timer-controls{display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%;flex-direction:row;grid-gap:1rem;gap:1rem}.timer__control{color:#fdfffc;font-size:125%;border-radius:4px;border:0;padding:1rem 2rem;cursor:pointer}.timer__control.start{background-color:#0e83d6}.timer__control.pause{background-color:#787883}.timer__control.reset{background-color:#e71d36}.timer__control:hover{opacity:0.5}#timer-length-settings{width:100%;max-width:800px;display:grid;grid-gap:1rem;gap:1rem}@media only screen and (min-width: 720px){#timer-length-settings{grid-gap:2rem;gap:2rem;grid-template-columns:1fr 1fr}}.timer-length{display:grid;grid-template:1fr / 3fr auto;grid-gap:1rem;gap:1rem;align-items:center;width:100%}.timer-length__controls{display:flex;justify-content:space-between;align-items:center}.timer-length__control{background-color:#0e83d6;color:#fdfffc;font-size:150%;font-weight:700;width:44px;height:44px;border:0;cursor:pointer}.timer-length__control.substract{border-radius:4px 0 0 4px}.timer-length__control.add{border-radius:0 4px 4px 0}.timer-length__control.not-allowed{cursor:not-allowed;background-color:#787883}.timer-length__control:hover:not(.not-allowed){opacity:0.5}.timer-length__value{display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%;background-color:#fdfffc;color:#011627;font-size:115%;width:44px;height:44px;margin:0}#author{display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%;flex-direction:row}#author a{color:#ff9f1c;text-decoration:none}#author a:hover{text-decoration:underline}

